@use 'base/variables' as v;
@use 'base/mixins' as m;

.about-room {
    font-family: v.$video_font;
    padding: 5rem 0;
    @include m.tablet {
        @include m.grid(2, 5rem);
    }

    img {
        max-width: 100%;
        display: block;
        
    }
}


.room-cotainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;

    @include m.tablet {
        margin-top: 0;
    }

    h2 {
        margin-bottom: 2rem;
    }

    .date {
        color: v.$rosa;
        font-weight: 700;
    }
}
