@use 'base/mixins' as m;
@use 'base/variables' as v;

.tickets {
    padding: 5rem 0;
    h3 {
        text-align: center;
        color: v.$blanco;
        margin-bottom: 2rem
    }

    .tickets-container {


        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    }
}

.pass {

    color: v.$blanco;

    .pass-name {
        text-align: center;
        font-size: 2.4rem;
        color: v.$blanco;
    }

    &-header,
    &-footer {
        padding: 0.5rem;
        p{
            text-align: center;
            color: v.$blanco;
            margin: 0;
        }
    }

    &-header {
        background-color: v.$azul;
        color: v.$blanco;
        p {
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }

    &-body {
        ul {
            color: v.$negro;
            @include m.list-reset;
            li, :nth-child(odd) {
                background-color: v.$blanco;
                font-size: 1.1rem;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;

            }
        }
    }

    &-footer {
        background-color: v.$rojo;
        color: v.$blanco;
        p {
            font-size: 2.4rem;
            font-weight: 500;
        }
    }
}