@use 'base/variables' as v;
@use 'base/mixins' as m;


.header {
    background-color: v.$negro;

    @include m.tablet {
        &.fix {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: 0.3rem 0.3rem 0.3rem #333 ;
        }
    }

    h1 {
        font-size: 5rem;
        color: v.$blanco;
        text-align: center;

        @include m.tablet{
            font-size: 6.5rem;
            margin: 0;
        }
        
    }

    .container-header {
        font-family: v.$fuente_principal;
        padding: 2rem 0 0;

        align-items: center;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }

    .logo {

        text-align: center;
        
        padding: 0 0.5rem 0 0;

        img {
            max-width: 45px;
            height: auto;
        }

        @include m.tablet {
            max-width: 100px;
        }
    }
   
}

.main-navigation {

    @include m.tablet{
        display: flex;
        gap: 1.6rem;
        margin-left: auto;
    }

    a {
        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2.9rem;
        margin-top: 1.8rem;

        @include m.tablet {
            margin: 0;
            font-size: 3.2rem;
        }

        &:hover {
            color: v.$rojo;
        }

    }

    .ticket {
        margin: 0;
    }
}