html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Noto Serif", serif;
  font-size: 1.6 rem;
}
body.fix-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 100%;
  height: auto;
}

.header {
  background-color: #000000;
}
@media (min-width: 768px) {
  .header.fix {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.3rem 0.3rem 0.3rem #333;
  }
}
.header h1 {
  font-size: 5rem;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    font-size: 6.5rem;
    margin: 0;
  }
}
.header .container-header {
  font-family: "Cute Font", sans-serif;
  padding: 2rem 0 0;
  align-items: center;
}
@media (min-width: 768px) {
  .header .container-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header .logo {
  text-align: center;
  padding: 0 0.5rem 0 0;
}
.header .logo img {
  max-width: 45px;
  height: auto;
}
@media (min-width: 768px) {
  .header .logo {
    max-width: 100px;
  }
}

@media (min-width: 768px) {
  .main-navigation {
    display: flex;
    gap: 1.6rem;
    margin-left: auto;
  }
}
.main-navigation a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2.9rem;
  margin-top: 1.8rem;
}
@media (min-width: 768px) {
  .main-navigation a {
    margin: 0;
    font-size: 3.2rem;
  }
}
.main-navigation a:hover {
  color: #f10408;
}
.main-navigation .ticket {
  margin: 0;
}

.video {
  position: relative;
  height: 50vh;
  overflow: hidden;
}
@media (min-width: 768px) {
  .video {
    height: 75vh;
  }
}
.video .overlay {
  position: absolute;
  background: rgb(0, 0, 0);
  background: rgb(0, 0, 0);
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 42%, rgba(255, 255, 255, 0.12) 95%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .video .overlay {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 42%, rgba(255, 255, 255, 0.12) 77%);
  }
}
.video video {
  width: 100%;
  height: auto;
  min-height: 100%;
  min-width: 100%;
  object-fit: cover;
}

.video-container {
  z-index: 1;
  font-style: "Noto Serif", serif;
}
.video-container h2 {
  color: #FFFFFF;
  font-size: 2.6rem;
  margin: 0;
}
@media (min-width: 768px) {
  .video-container h2 {
    font-size: 4rem;
  }
}
.video-container p {
  background-color: #f10408;
  display: inline-block;
  padding: 0.8rem 0.5rem;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.1rem;
  margin: 0;
}
@media (min-width: 768px) {
  .video-container p {
    font-size: 1.5rem;
    padding: 1rem 2rem;
  }
}

.about-room {
  font-family: "Noto Serif", serif;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-room {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.about-room img {
  max-width: 100%;
  display: block;
}

.room-cotainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .room-cotainer {
    margin-top: 0;
  }
}
.room-cotainer h2 {
  margin-bottom: 2rem;
}
.room-cotainer .date {
  color: #F53756;
  font-weight: 700;
}

.lineup {
  background-color: #FF1818;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFFFFF;
}
.lineup .day {
  text-transform: uppercase;
  margin-bottom: 5rem 0;
}

@media (min-width: 768px) {
  .scenery-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.scenery-container ul {
  color: #FFFFFF;
}

.scenary {
  padding-top: 2rem;
}
.scenary p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 6rem;
  background-size: 8rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 1rem auto 0 auto;
}

.wave .scenary-name:after {
  background-image: url(../../../build/img/synth.webp);
}

.dj .scenary-name:after {
  background-image: url(../../../build/img/torna.avif);
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  padding: 2.5rem;
  font-size: 1.3rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 200;
}

.bg-one, .bg-two {
  background-color: #18171c;
}
.bg-one li:nth-child(odd), .bg-two li:nth-child(odd) {
  background-color: #050505;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}
.gallery img {
  width: 163.94px;
  height: 109.38px;
}

.image-galery {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .image-galery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  .image-galery img {
    width: 133.88 0.15px;
    height: 89.5px;
  }
}
@media (min-width: 768px) {
  .image-galery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
  .image-galery img {
    width: 163.6px;
    height: 89.88px;
  }
}
@media (min-width: 992px) {
  .image-galery {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
  .image-galery img {
    width: 142.5px;
    height: 95.7px;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay .picture {
  display: block;
  max-width: 80rem;
}

.btn-close {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-close:hover {
  cursor: pointer;
}

.tickets {
  padding: 5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tickets .tickets-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass {
  color: #FFFFFF;
}
.pass .pass-name {
  text-align: center;
  font-size: 2.4rem;
  color: #FFFFFF;
}
.pass-header, .pass-footer {
  padding: 0.5rem;
}
.pass-header p, .pass-footer p {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
}
.pass-header {
  background-color: #424242;
  color: #FFFFFF;
}
.pass-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass-body ul {
  color: #000000;
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass-body ul li, .pass-body ul :nth-child(odd) {
  background-color: #FFFFFF;
  font-size: 1.1rem;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pass-footer {
  background-color: #f10408;
  color: #FFFFFF;
}
.pass-footer p {
  font-size: 2.4rem;
  font-weight: 500;
}

.site-footer {
  background-color: #f10408;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
}