@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    height: 50vh;
    overflow: hidden;

    @include m.tablet {
        height: 75vh;
    }

    .overlay {
        position: absolute;
        background: rgb(0,0,0);
        background: rgb(0,0,0);
        background: linear-gradient(180deg, rgba(0,0,0,0.6) 42%, rgba(255,255,255,0.12) 95%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        @include m.tablet {
            background: linear-gradient(180deg, rgba(0,0,0,0.6) 42%, rgba(255,255,255,0.12) 77%);
        }
    }

    video {
        width: 100%;
        height: auto;
        min-height: 100%;
        min-width: 100%;
        object-fit: cover;
    }
}

.video-container {

    z-index: 1;
    font-style: v.$video_font;

    h2 {
        color: v.$blanco;
        font-size: 2.6rem;
        margin: 0;

        @include m.tablet {
            font-size: 4rem;
        }
    }

    p {
        background-color: v.$rojo;
        display: inline-block;
        padding: 0.8rem 0.5rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 1.1rem;
        margin: 0;

        @include m.tablet {
            font-size: 1.5rem;
            padding: 1rem 2rem;
        }
    }
}