@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup {
    background-color: v.$redneon;
    padding: 5rem 0;

    h3, p {
        text-align: center;
        color: v.$blanco
    }

    .day {
        text-transform: uppercase;
        margin-bottom: 5rem 0;
    }
}

.scenery-container {
    @include m.tablet {
        @include m.grid(2, 5rem);
    }

    ul {
        color: v.$blanco;
    }

}

.scenary {
    padding-top: 2rem;

    p {

        &:after {
            content: '';
            display: block;
            width: 20rem;
            height: 6rem;
            background-size: 8rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 1rem auto 0 auto;
        }
    }
}

.wave .scenary-name:after {
    background-image: url(../../../build/img/synth.webp);
}

.dj .scenary-name:after {
    background-image: url(../../../build/img/torna.avif);
}

.calendar {
    @include m.list-reset;

    li {
        padding: 2.5rem;
        font-size: 1.3rem;
        font-weight: 700;
        span {
            font-weight: 200;
        }
    }
}

.bg-one, .bg-two {
    background-color: v.$bluenegry;;

    li:nth-child(odd) {
        background-color: darken(v.$blacktraf, 10%);
    }
}