@use 'base/mixins' as m;
@use 'base/variables' as v;

.gallery {
    padding: 5rem 0;

    h3 {
        text-align: center;
    }

    img {
        width: 163.94px;
        height: 109.38px;
    }
}

.image-galery {
    @include m.list-reset;
    @include m.grid(2, 2rem);

    @include m.phone{
        @include m.grid(3,2rem);

        img {
            width: 133.88.15px;
            height: 89.5px;
        }
    }

    @include m.tablet{
        @include m.grid(4,2rem);

        img {
            width: 163.6px;
            height: 89.88px;
        }
    }

    @include m.desktop{
        @include m.grid(6,2rem);

        img {
            width: 142.5px;
            height: 95.7px;
        }

    }
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / 0.5);

    .picture {
        display: block;
        max-width: 80rem;
    }

}

.btn-close {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover {
        cursor: pointer;
    }
}